@import url(https://fonts.googleapis.com/css?family=Coming+Soon);
body {
	margin: 0;
	padding: 0;
	font-family: "Coming Soon", cursive;
}

.App {
	text-align: center;
	height: auto;
	display: flex;
	align-items: center;
	justify-content: center;
	background: url("https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/intermediary/f/61f7685f-47e5-49a0-b5cb-ca6b8cab4228/drok89-a7a2a550-ba87-40a6-9701-b635550d55b3.jpg");
	background-position: center center;
}

.Hangman {
	display: block;
	width: 100vw;
	height: 100vh;
	color: black;
	/* text-align: center; */
}

.Hangman-title {
	display: flex;
	justify-content: center;
	height: 10vh;
}

.Hangman-main {
	display: flex;
	flex-flow: row wrap;
	align-items: center;
	justify-content: space-evenly;
	height: 100vh;
	width: 100vw;
}

.Hangman-image-section {
	flex-direction: row;
}

.Hangman-word-and-btns-section {
	flex-direction: row;
}

.Hangman img {
	width: 20rem;
	border: 10px solid white;
	box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27), 0 0 40px rgba(0, 0, 0, 0.06) inset;
}

.Hangman h1 {
	font-size: 4rem;
	font-weight: 100;
	margin-bottom: 0.1em;
	margin-top: 0;
}

.Hangman-word {
	letter-spacing: 1em;
	margin: 0.2em 0;
	font-size: 3rem;
}

.Hangman-guess-count {
	font-size: 30px;
	margin: 0;
}

.Hangman-btns {
	text-align: center;
	display: inline-block;
	width: 26rem;
	margin-top: 0;
	font-size: 30px;
}

.Hangman button {
	border: none;
	font-family: inherit;
	font-size: 2.7rem;
	cursor: pointer;
	width: 4rem;
	margin: 7px 2px 7px 2px;
	letter-spacing: 2px;
	outline: none;
	background: #ffc107;
	color: #fff;
	box-shadow: 0 12px #ff9800;
	border-radius: 5px;
	padding-top: 4px;
}

.Hangman button:hover {
	background-color: #ff9800; /* Background color of button now the same as to box shadow  */
}

.Hangman button:disabled {
	background: #bdbdbd;
	color: #eeeeee;
	box-shadow: 0 5px #9e9e9e;
	transform: translate(0, 0.15rem);
}

#reset {
	width: auto;
	display: block;
	margin: 0 auto;
	background-color: rgb(138 43 226 / 80%);
	box-shadow: 0 6px rgb(138 43 226);
}

#reset:hover {
	-webkit-filter: brightness(110%);
	        filter: brightness(110%);
}

#reset:active {
	transform: translate(0, 0.3rem);
	box-shadow: 0 3px rgb(138 43 226);
}

